.SideDrawer {
  position: fixed;
  width: 9rem;
  max-width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 200;
  background-color: #343940;
  padding: 2rem 1rem;
  transition: transform 0.3s ease-out;
}

@media (min-width: 510px) {
  .SideDrawer {
    display: none;
  }
}

.Open {
  transform: translateX(0);
}

.Close {
  transform: translateX(-100%);
}
